import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isOpen: false,
    isHome: true,
    symptomArea: ['','','',''],
    symptomName: ['','',''],
    ages: ['','','','',''],
    genders: ['',''],
    pharmacy: [
      {
        imgSrc: '/images/ildong.png',
        name: ''
      },
      {
        imgSrc: '/images/dongkook.png',
        name: ''
      },
      {
        imgSrc: '/images/dongkook.png',
        name: ''
      },
      {
        imgSrc: '/images/dongkook.png',
        name: ''
      }
    ]
  },
  getters: {
    getIsHome(state) {
      return state.isHome;
    },
    getPharmacy(state) {
      return state.pharmacy
    },
    getIsOpen(state) {
      return state.isOpen
    }
  },
  mutations: {
    setIsHome(state, bool) {
      state.isHome = bool;
    },
    toggleIsOpen(state) {
      state.isOpen = !state.isOpen;
    },
    setIsOpen(state, bool) {
      state.isOpen = bool;
    }
  },
  actions: {
  },
  modules: {
  }
})
